import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import { Editor, Range, Transforms } from 'slate'

import { replaceSymbols } from '../../utils/util'
import { LinkElement } from './custom-types'

/**
 * æ ¡éª link
 * @param menuKey menu key
 * @param editor editor
 * @param text menu text
 * @param url menu url
 */
async function check(
  menuKey: string,
  editor: IDomEditor,
  text: string,
  url: string,
): Promise<boolean> {
  const { checkLink } = editor.getMenuConfig(menuKey)

  if (checkLink) {
    const res = await checkLink(text, url)

    if (typeof res === 'string') {
      // æ£éªæªéè¿ï¼æç¤ºä¿¡æ¯
      editor.alert(res, 'error')
      return false
    }
    if (res == null) {
      // æ£éªæªéè¿ï¼ä¸æç¤ºä¿¡æ¯
      return false
    }
  }

  return true // æ ¡éªéè¿
}

/**
 * è½¬æ¢é¾æ¥ url
 * @param menuKey menu key
 * @param editor editor
 * @param url url
 * @returns parsedUrl
 */
async function parse(menuKey: string, editor: IDomEditor, url: string): Promise<string> {
  const { parseLinkUrl } = editor.getMenuConfig(menuKey)

  if (parseLinkUrl) {
    const newUrl = await parseLinkUrl(url)

    return newUrl
  }
  return url
}

export function isMenuDisabled(editor: IDomEditor): boolean {
  if (editor.selection == null) { return true }

  const selectedElems = DomEditor.getSelectedElems(editor)
  const notMatch = selectedElems.some(elem => {
    const { type } = elem

    if (editor.isVoid(elem)) { return true }
    if (['pre', 'code', 'link'].includes(type)) { return true }
    return false
  })

  if (notMatch) { return true } // disabled
  return false // enable
}

/**
 * çæ link node
 * @param url url
 * @param text text
 */
function genLinkNode(url: string, text?: string): LinkElement {
  const linkNode: LinkElement = {
    type: 'link',
    url: replaceSymbols(url),
    children: text ? [{ text }] : [],
  }

  return linkNode
}

/**
 * æå¥ link
 * @param editor editor
 * @param text text
 * @param url url
 */
export async function insertLink(editor: IDomEditor, text: string, url: string) {
  if (!url) { return }
  if (!text) { text = url } // æ  text åç¨ url ä»£æ¿

  // è¿åéåº
  editor.restoreSelection()

  if (isMenuDisabled(editor)) { return }

  // æ ¡éª
  const checkRes = await check('insertLink', editor, text, url)

  if (!checkRes) { return } // æ ¡éªæªéè¿

  // è½¬æ¢ url
  const parsedUrl = await parse('insertLink', editor, url)

  // å¤æ­éåºæ¯å¦æå 
  const { selection } = editor

  if (selection == null) { return }
  const isCollapsed = Range.isCollapsed(selection)

  // æ§è¡ï¼æå¥é¾æ¥
  if (isCollapsed) {
    const leftLength = DomEditor.getLeftLengthOfMaxLength(editor)

    if (leftLength <= 0) {
      // å·²ç»è§¦å maxLength ï¼ä¸åè¾å¥æå­
      return
    }

    // é¾æ¥ååæå¥ç©ºæ ¼ï¼æ¹ä¾¿æä½
    editor.insertText(' ')

    if (leftLength < text.length + 1) {
      // å©ä½é¿åº¦å°äº text é¿åº¦ï¼åæªå text
      text = text.slice(0, leftLength - 1)
    }

    const linkNode = genLinkNode(parsedUrl, text)

    Transforms.insertNodes(editor, linkNode)

    // https://github.com/wangeditor-next/wangeditor-next/issues/332
    // ä¸è½ç´æ¥ä½¿ç¨ insertText, ä¼é ææ·»å çç©ºæ ¼è¢«æ·»å å°é¾æ¥ææ¬ä¸­ï¼åèä¸é¢ issueï¼æ¿æ¢ä¸º insertFragment æ¹å¼æ·»å ç©ºæ ¼
    editor.insertFragment([{ text: ' ' }])
  } else {
    const selectedText = Editor.string(editor, selection) // éä¸­çæå­

    if (selectedText !== text) {
      // éä¸­çæå­åè¾å¥çæå­ä¸ä¸æ ·ï¼åå ææå­ï¼æå¥é¾æ¥

      const leftLength = DomEditor.getLeftLengthOfMaxLength(editor)

      if (leftLength <= 0) {
        // å·²ç»è§¦å maxLength ï¼ä¸åè¾å¥æå­
        return
      }
      if (leftLength < selectedText.length - text.length) {
        // å©ä½é¿åº¦å°äº text é¿åº¦ï¼åæªå text
        text = text.slice(0, leftLength)
      }

      editor.deleteFragment()
      const linkNode = genLinkNode(parsedUrl, text)

      Transforms.insertNodes(editor, linkNode)
    } else {
      // éä¸­çæå­åè¾å¥çæå­ä¸æ ·ï¼ååªåè£¹é¾æ¥å³å¯
      const linkNode = genLinkNode(parsedUrl)

      Transforms.wrapNodes(editor, linkNode, { split: true })
      Transforms.collapse(editor, { edge: 'end' })
    }
  }
}

/**
 * ä¿®æ¹ link url
 * @param editor editor
 * @param text text
 * @param url link url
 */
export async function updateLink(editor: IDomEditor, text: string, url: string) {
  if (!url) { return }

  // æ ¡éª
  const checkRes = await check('editLink', editor, text, url)

  if (!checkRes) { return } // æ ¡éªæªéè¿

  // è½¬æ¢ url
  const parsedUrl = await parse('editLink', editor, url)

  // ä¿®æ¹é¾æ¥
  const props: Partial<LinkElement> = { url: replaceSymbols(parsedUrl) }

  Transforms.setNodes(editor, props, {
    match: n => DomEditor.checkNodeType(n, 'link'),
  })
}
