import { IButtonMenu, IDomEditor } from '@wangeditor-next/core'
import { Editor } from 'slate'

import { isMenuDisabled } from '../helper'

abstract class BaseMenu implements IButtonMenu {
  abstract readonly mark: string

  protected readonly marksNeedToRemove: string[] = [] // å¢å  mark çåæ¶ï¼éè¦ç§»é¤åªäº mark ï¼äºæ¥ï¼ä¸è½å±å­çï¼

  abstract readonly title: string

  abstract readonly iconSvg: string

  abstract readonly hotkey: string

  readonly tag = 'button'

  /**
   * è·åï¼æ¯å¦æ mark
   * @param editor editor
   */
  getValue(editor: IDomEditor): string | boolean {
    const mark = this.mark
    const curMarks = Editor.marks(editor)

    // å½ curMarks å­å¨æ¶ï¼è¯´æç¨æ·æå¨è®¾ç½®ï¼ä»¥ curMarks ä¸ºå
    if (curMarks) {
      return curMarks[mark]
    }
    const [match] = Editor.nodes(editor, {
      // @ts-ignore
      match: n => n[mark] === true,
    })

    return !!match

  }

  isActive(editor: IDomEditor): boolean {
    const isMark = this.getValue(editor)

    return !!isMark
  }

  isDisabled(editor: IDomEditor): boolean {
    return isMenuDisabled(editor, this.mark)
  }

  /**
   * æ§è¡å½ä»¤
   * @param editor editor
   * @param value æ¯å¦æ mark
   */
  exec(editor: IDomEditor, value: string | boolean) {
    const { mark, marksNeedToRemove } = this

    if (value) {
      // å·²ï¼ååæ¶
      editor.removeMark(mark)
    } else {
      // æ²¡æï¼åæ§è¡
      editor.addMark(mark, true)

      // ç§»é¤äºæ¥ãä¸è½å±å­ç marks
      if (marksNeedToRemove) {
        marksNeedToRemove.forEach(m => editor.removeMark(m))
      }
    }
  }
}

export default BaseMenu
