import { IDomEditor } from '@wangeditor-next/core'
import {
  Editor,
  Element as SlateElement,
  Node as SlateNode,
  Text as SlateText,
  Transforms,
} from 'slate'

function deleteHandler(newEditor: IDomEditor): boolean {
  const [nodeEntry] = Editor.nodes(newEditor, {
    match: n => newEditor.children[0] === n, // editor ç¬¬ä¸ä¸ªèç¹
    mode: 'highest', // æé«å±çº§
  })

  if (nodeEntry == null) { return false }

  const n = nodeEntry[0]

  if (!SlateElement.isElement(n)) { return false }
  if (n.type === 'paragraph') { return false } // å½ä¸­äº paragraph ï¼åä¸åç»§ç»­å¤æ­
  if (SlateNode.string(n) !== '') { return false } // æªå é¤å¨é¨åå®¹ï¼åä¸åç»§ç»­å¤æ­

  const { children = [] } = n

  if (!SlateText.isText(children[0])) { return false } // n.children ä¸æ¯ text ï¼å¦ tableï¼ï¼åä¸åç»§ç»­å¤æ­

  // è³æ­¤ï¼å°±å½ä¸­äºä¸ä¸ªï¼é paragraphï¼+ï¼children é½æ¯ textï¼+ï¼åå®¹ä¸ºç©ºï¼çé¡¶çº§ node ï¼å¦ header blockQuote ç­
  // ç¶åï¼å°å¶å´æ¢ä¸º paragraph
  Transforms.setNodes(newEditor, {
    type: 'paragraph',
  })
  return true
}

function withParagraph<T extends IDomEditor>(editor: T): T {
  const {
    deleteBackward, deleteForward,
  } = editor
  const newEditor = editor

  // å é¤é p çææ¬ elemï¼å¦ header blockQuote ç­ï¼ï¼å é¤æ²¡æåå®¹æ¶ï¼åæ¢ä¸º p
  newEditor.deleteBackward = unit => {
    const res = deleteHandler(newEditor)

    if (res) { return } // å½ä¸­ç»æï¼å return

    // æ§è¡é»è®¤çå é¤
    deleteBackward(unit)
  }
  newEditor.deleteForward = unit => {
    const res = deleteHandler(newEditor)

    if (res) { return } // å½ä¸­ç»æï¼å return

    // æ§è¡é»è®¤çå é¤
    deleteForward(unit)
  }

  // è¿å editor ï¼éè¦ï¼
  return newEditor
}

export default withParagraph
