if (typeof global === 'object') {
  // ç¨äº nodejs ï¼é¿åæ¥é
  const globalProperty = Object.getOwnPropertyDescriptor(global, 'window')

  // global.window ä¸ºç©ºåç´æ¥åå¥
  // é¨åæ¡æ¶ä¸å·²ç»å®ä¹äºglobal.windowä¸æ¯ä¸å¯åå±æ§
  if (!global.window || globalProperty.set) {
    global.window = global
    global.requestAnimationFrame = () => {}
    global.navigator = {
      userAgent: '',
    }
    global.location = {
      hostname: '0.0.0.0',
      port: 0,
      protocol: 'http:',
    }
    global.btoa = () => {}
    global.crypto = {
      getRandomValues: function (buffer: any) {
        return nodeCrypto.randomFillSync(buffer)
      },
    }
  }

  if (global.document != null) {
    // SSR ç¯å¢ä¸å¯è½ä¼æ¥é ï¼issue 4409ï¼
    if (global.document.getElementsByTagName == null) {
      global.document.getElementsByTagName = () => []
    }
  }
}
