import { nanoid } from 'nanoid'

/**
 * è·åéæºæ°å­ç¬¦ä¸²
 * @param prefix åç¼
 * @returns éæºæ°å­ç¬¦ä¸²
 */
export function genRandomStr(prefix = 'r'): string {
  return `${prefix}-${nanoid()}`
}

export function replaceSymbols(str: string) {
  return str.replace(/</g, '&lt;').replace(/>/g, '&gt;')
}

export function styleStringToObject(styleString) {
  const styleObject = {}

  // å»æå­ç¬¦ä¸²ä¸¤ç«¯çç©ºæ ¼ï¼ç¶åæåå·åå²
  const styles = styleString.trim().split(';')

  // è¿­ä»£æ¯ä¸ä¸ªæ ·å¼å±æ§å¯¹
  styles.forEach(style => {
    if (style) {
      // å¿½ç¥ç©ºå­ç¬¦ä¸²
      const [property, value] = style.split(':')

      if (property && value) {
        // å»æä¸¤ç«¯çç©ºæ ¼å¹¶å°ç»æå­å¨å¨å¯¹è±¡ä¸­
        styleObject[property.trim()] = value.trim()
      }
    }
  })

  return styleObject
}
