import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import { Element as SlateElement } from 'slate'
import { h, VNode } from 'snabbdom'

function renderDivider(
  elemNode: SlateElement,
  children: VNode[] | null,
  editor: IDomEditor,
): VNode {
  const renderStyle: any = {}

  // æ¯å¦éä¸­
  const selected = DomEditor.isNodeSelected(editor, elemNode)

  const vnode = h(
    'div',
    {
      props: {
        contentEditable: false,
        className: 'w-e-textarea-divider',
      },
      dataset: {
        selected: selected ? 'true' : '',
      },
      style: renderStyle,
      on: {
        mousedown: event => event.preventDefault(),
      },
    },
    [h('hr')],
  )
  // ãæ³¨æãvoid node ä¸­ï¼renderElem ä¸ç¨å¤ç children ãcore ä¼ç»ä¸å¤çã

  return vnode
}

const renderDividerConf = {
  type: 'divider', // å elemNode.type ä¸è´
  renderElem: renderDivider,
}

export { renderDividerConf }
