import {
  DomEditor, IDomEditor, IDropPanelMenu, t,
} from '@wangeditor-next/core'
import { Editor } from 'slate'

import { EMOTION_SVG } from '../../../constants/icon-svg'
import $, { Dom7Array, DOMElement } from '../../../utils/dom'

class EmotionMenu implements IDropPanelMenu {
  readonly title = t('emotion.title')

  readonly iconSvg = EMOTION_SVG

  readonly tag = 'button'

  readonly showDropPanel = true // ç¹å» button æ¶æ¾ç¤º dropPanel

  private $content: Dom7Array | null = null

  exec(_editor: IDomEditor, _value: string | boolean) {
    // ç¹å»èåæ¶ï¼å¼¹åº droPanel ä¹åï¼ä¸éè¦æ§è¡å¶ä»ä»£ç 
    // æ­¤å¤ç©ºçå³å¯
  }

  getValue(_editor: IDomEditor): string | boolean {
    // ä¸éè¦ getValue
    return ''
  }

  isActive(_editor: IDomEditor): boolean {
    // ä¸éè¦ active
    return false
  }

  isDisabled(editor: IDomEditor): boolean {
    if (editor.selection == null) { return true }

    const [match] = Editor.nodes(editor, {
      match: n => {
        const type = DomEditor.getNodeType(n)

        if (type === 'pre') { return true } // ä»£ç å
        if (Editor.isVoid(editor, n)) { return true } // void node

        return false
      },
      universal: true,
    })

    if (match) { return true }
    return false
  }

  getPanelContentElem(editor: IDomEditor): DOMElement {
    if (this.$content == null) {
      // ç¬¬ä¸æ¬¡æ¸²æ
      const $content = $('<ul class="w-e-panel-content-emotion"></ul>')

      // ç»å®äºä»¶ï¼ä»ç¬¬ä¸æ¬¡ç»å®ï¼ä¸å¯éå¤ç»å®ï¼
      $content.on('click', 'li', (e: Event) => {
        const { target } = e

        if (target == null) { return }
        e.preventDefault()

        const $li = $(target)
        const emotionStr = $li.text()

        editor.insertText(emotionStr)
      })

      this.$content = $content
    }

    const $content = this.$content

    if ($content == null) { return document.createElement('ul') }
    $content.empty() // æ¸ç©ºä¹ååéç½®åå®¹

    // è·åèåéç½®
    const colorConf = editor.getMenuConfig('emotion')
    const { emotions = [] } = colorConf
    // æ ¹æ®èåéç½®çæ panel content

    emotions.forEach((emotion: string) => {
      const $li = $(`<li>${emotion}</li>`)

      $content.append($li)
    })

    return $content[0]
  }
}

export default EmotionMenu
