import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import { Transforms } from 'slate'

function withDivider<T extends IDomEditor>(editor: T): T {
  const { isVoid, normalizeNode } = editor
  const newEditor = editor

  // éå isVoid
  newEditor.isVoid = elem => {
    const { type } = elem

    if (type === 'divider') {
      return true
    }

    return isVoid(elem)
  }

  // éæ° normalize
  newEditor.normalizeNode = ([node, path]) => {
    const type = DomEditor.getNodeType(node)

    if (type !== 'divider') {
      // æªå½ä¸­ divider ï¼æ§è¡é»è®¤ç normalizeNode
      return normalizeNode([node, path])
    }

    // -------------- divider æ¯ editor æåä¸ä¸ªèç¹ï¼éè¦åé¢æå¥ p --------------
    const isLast = DomEditor.isLastNode(newEditor, node)

    if (isLast) {
      Transforms.insertNodes(newEditor, DomEditor.genEmptyParagraph(), { at: [path[0] + 1] })
    }
  }

  // è¿å editor ï¼éè¦ï¼
  return newEditor
}

export default withDivider
