import { isInsertImageMenuDisabled } from '@wangeditor-next/basic-modules'
import { IDomEditor } from '@wangeditor-next/core'

import uploadImages from './upload-images'

function withUploadImage<T extends IDomEditor>(editor: T): T {
  const { insertData } = editor
  const newEditor = editor

  // éå insertData - ç²è´´å¾çãææ½ä¸ä¼ å¾ç
  newEditor.insertData = (data: DataTransfer) => {
    if (isInsertImageMenuDisabled(newEditor)) {
      insertData(data)
      return
    }

    // å¦æ text ï¼åä¼åç²è´´ text
    const text = data.getData('text/plain')

    if (text) {
      insertData(data)
      return
    }

    // è·åæä»¶
    const { files } = data

    if (files.length <= 0) {
      insertData(data)
      return
    }

    // å¤æ­æ¯å¦æå¾çæä»¶ï¼å¯è½æ¯å¶ä»ç±»åçæä»¶ï¼
    const fileList = Array.prototype.slice.call(files)
    const hasImageFiles = fileList.some(file => {
      const [mime] = file.type.split('/')

      return mime === 'image'
    })

    if (hasImageFiles) {
      // æå¾çæä»¶ï¼åä¸ä¼ å¾ç
      uploadImages(editor, files)
    } else {
      // å¦ææ²¡æï¼ åç»§ç»­ insertData
      insertData(data)
    }
  }

  // è¿å editor ï¼éè¦ï¼
  return newEditor
}

export default withUploadImage
