import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import { Transforms } from 'slate'

import { CustomElement } from '../../../custom-types'

function withVideo<T extends IDomEditor>(editor: T): T {
  const { isVoid, normalizeNode } = editor
  const newEditor = editor

  // éå isVoid
  newEditor.isVoid = (elem: CustomElement) => {
    const { type } = elem

    if (type === 'video') {
      return true
    }

    return isVoid(elem)
  }

  // éå normalizeNode
  newEditor.normalizeNode = ([node, path]) => {
    const type = DomEditor.getNodeType(node)

    // ----------------- video åé¢å¿é¡»è·ä¸ä¸ª p header blockquote -----------------
    if (type === 'video') {
      // -------------- video æ¯ editor æåä¸ä¸ªèç¹ï¼éè¦åé¢æå¥ p --------------
      const isLast = DomEditor.isLastNode(newEditor, node)

      if (isLast) {
        Transforms.insertNodes(newEditor, DomEditor.genEmptyParagraph(), { at: [path[0] + 1] })
      }
    }

    // æ§è¡é»è®¤ç normalizeNode ï¼éè¦ï¼ï¼ï¼
    return normalizeNode([node, path])
  }

  // è¿å editor ï¼éè¦ï¼
  return newEditor
}

export default withVideo
