import Uppy, { UppyFile } from '@uppy/core'
import { insertImageNode } from '@wangeditor-next/basic-modules'
import { createUploader, IDomEditor } from '@wangeditor-next/core'

// å­å¨ editor uppy çå³ç³» - ç¼å­ uppy ï¼ä¸éå¤åå»º
const EDITOR_TO_UPPY_MAP = new WeakMap<IDomEditor, Uppy>()

function getMenuConfig(editor: IDomEditor) {
  return editor.getMenuConfig('uploadImage')
}
/**
 * è·å uppy å®ä¾ï¼å¹¶éè¿ editor ç¼å­ï¼
 * @param editor editor
 */
function getUppy(editor: IDomEditor): Uppy {
  // ä»ç¼å­ä¸­è·å
  let uppy = EDITOR_TO_UPPY_MAP.get(editor)

  if (uppy != null) { return uppy }

  const menuConfig = getMenuConfig(editor)
  const {
    onSuccess, onProgress, onFailed, customInsert, onError,
  } = menuConfig

  // ä¸ä¼ å®æä¹å
  const successHandler = (file: UppyFile, res: any) => {
    // é¢æ res æ ¼å¼ï¼
    // æåï¼{ errno: 0, data: { url, alt, href } } ââ æ³¨æï¼æ§çç data æ¯æ°ç»ï¼è¦å¼å®¹ä¸ä¸
    // å¤±è´¥ï¼{ errno: !0, message: 'å¤±è´¥ä¿¡æ¯' }

    if (customInsert) {
      // ç¨æ·èªå®ä¹æå¥å¾çï¼æ­¤æ¶ res æ ¼å¼å¯è½ä¸ç¬¦åé¢æ
      customInsert(res, (src, alt, href) => insertImageNode(editor, src, alt, href))
      // success åè°
      onSuccess(file, res)
      return
    }

    const { errno = 1, data = {} } = res

    if (errno !== 0) {
      // failed åè°
      onFailed(file, res)
      return
    }

    if (Array.isArray(data)) {
      // è¿åçæ°ç»ï¼æ§ççï¼å¼å®¹ä¸ä¸ï¼
      data.forEach((item: { url: string; alt?: string; href?: string }) => {
        const { url = '', alt = '', href = '' } = item
        // ä½¿ç¨ basic-module ç insertImageNode æ¹æ³æå¥å¾çï¼å¶ä¸­æç¨æ·éç½®çæ ¡éªå callback

        insertImageNode(editor, url, alt, href)
      })
    } else {
      // è¿åçå¯¹è±¡
      const { url = '', alt = '', href = '' } = data

      insertImageNode(editor, url, alt, href)
    }

    // success åè°
    onSuccess(file, res)
  }

  // progress æ¾ç¤ºè¿åº¦æ¡
  const progressHandler = (progress: number) => {
    editor.showProgressBar(progress)

    // åè°å½æ°
    if (onProgress) { onProgress(progress) }
  }

  // onError æç¤ºéè¯¯
  const errorHandler = (file: any, err: any, res: any) => {
    // åè°å½æ°
    onError(file, err, res)
  }

  // åå»º uppy
  uppy = createUploader({
    ...menuConfig,
    onProgress: progressHandler,
    onSuccess: successHandler,
    onError: errorHandler,
  })
  // ç¼å­ uppy
  EDITOR_TO_UPPY_MAP.set(editor, uppy)

  return uppy
}

/**
 * æå¥ base64 æ ¼å¼
 * @param editor editor
 * @param file file
 */
async function insertBase64(editor: IDomEditor, file: File) {
  return new Promise(resolve => {
    const reader = new FileReader()

    reader.readAsDataURL(file)
    reader.onload = () => {
      const { result } = reader

      if (!result) { return }
      const src = result.toString()
      const href = src.indexOf('data:image') === 0 ? '' : src // base64 æ ¼å¼åä¸è®¾ç½® href

      insertImageNode(editor, src, file.name, href)

      resolve('ok')
    }
  })
}

/**
 * ä¸ä¼ å¾çæä»¶
 * @param editor editor
 * @param file file
 */
async function uploadFile(editor: IDomEditor, files: File[]) {
  const uppy = getUppy(editor)
  const uploadList = files.map(file => ({
    name: file.name,
    type: file.type,
    size: file.size,
    data: file,
  }))

  uppy.addFiles(uploadList)
  await uppy.upload()
}

/**
 * ä¸ä¼ å¾ç
 * @param editor editor
 * @param files files
 */
export default async function (editor: IDomEditor, files: FileList | null) {
  if (files == null) { return }
  const fileList = Array.prototype.slice.call(files)

  // è·åèåéç½®
  const { customUpload, base64LimitSize } = getMenuConfig(editor)

  const uploadFileList : File[] = []
  // æé¡ºåºä¸ä¼ 

  for await (const file of fileList) {
    const size = file.size // size kb

    if (base64LimitSize && size <= base64LimitSize) {
      // åè®¸ base64 ï¼èä¸ size å¨ base64 éå¶ä¹åï¼åæå¥ base64 æ ¼å¼
      await insertBase64(editor, file)
    } else if (customUpload) {
      // èªå®ä¹ä¸ä¼ 
      await customUpload(file, (src, alt, href) => insertImageNode(editor, src, alt, href))
    } else {
      // é»è®¤ä¸ä¼ 
      uploadFileList.push(file)
    }
  }
  // é»è®¤ä¸ä¼ 
  if (uploadFileList.length > 0) { await uploadFile(editor, uploadFileList) }
}
