import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import { Editor, Transforms } from 'slate'

/**
 * è·å node typeï¼'header1' 'header2' ç­ï¼ï¼æªå¹éåè¿å 'paragraph'
 */
export function getHeaderType(editor: IDomEditor): string {
  const [match] = Editor.nodes(editor, {
    match: n => {
      const type = DomEditor.getNodeType(n)

      return type.startsWith('header') // å¹é node.type æ¯ header å¼å¤´ç node
    },
    universal: true,
  })

  // æªå¹éå° header
  if (match == null) { return 'paragraph' }

  // å¹éå° header
  const [n] = match

  return DomEditor.getNodeType(n)
}

export function isMenuDisabled(editor: IDomEditor): boolean {
  if (editor.selection == null) { return true }

  const [nodeEntry] = Editor.nodes(editor, {
    match: n => {
      const type = DomEditor.getNodeType(n)

      // åªå¯ç¨äº p å header
      if (type === 'paragraph') { return true }
      if (type.startsWith('header')) { return true }

      return false
    },
    universal: true,
    mode: 'highest', // å¹éæé«å±çº§
  })

  // å¹éå° p header ï¼ä¸ç¦ç¨
  if (nodeEntry) {
    return false
  }
  // æªå¹éå° p header ï¼åç¦ç¨
  return true
}

/**
 * è®¾ç½® node type ï¼'header1' 'header2' 'paragraph' ç­ï¼
 */
export function setHeaderType(editor: IDomEditor, type: string) {
  if (!type) { return }

  // æ§è¡å½ä»¤
  Transforms.setNodes(editor, {
    type,
  })
}
