import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import {
  Editor, Element as SlateElement, Path,
} from 'slate'
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import { jsx, VNode } from 'snabbdom'

import { ELEM_TO_EDITOR } from '../utils/maps'
import { getListItemColor } from '../utils/util'
import { ListItemElement } from './custom-types'

/**
 * æ åºåè¡¨ï¼æ ¹æ® level è·åçåç½®ç¬¦å·
 * @param level å±çº§
 */
function genPreSymbol(level = 0): string {
  let s = ''

  switch (level) {
    case 0:
      s = 'â¢' // ç¬¬ä¸å±çº§
      break
    case 1:
      s = 'â¦' // ç¬¬ä¸å±çº§
      break
    case 2:
      s = 'âª' // ç¬¬ä¸å±çº§
      break
    default:
      s = 'âª' // å¶ä»å±çº§
  }
  return s
}

/**
 * æåºåè¡¨ï¼è·ååç¼ number
 * @param editor editor
 * @param elem listItem elem
 */
function getOrderedItemNumber(editor: IDomEditor, elem: SlateElement): number {
  const { type, level = 0, ordered = false } = elem as ListItemElement

  let num = 1 // é»è®¤å¼ 1
  let curElem = elem
  let curPath = DomEditor.findPath(editor, curElem)

  // ç¬¬ä¸ä¸ªåç´ ï¼ç´æ¥è¿å 1
  if (curPath[0] === 0) { return 1 }

  while (curPath[0] > 0) {
    const prevPath = Path.previous(curPath)
    const prevEntry = Editor.node(editor, prevPath)

    if (prevEntry == null) { break }
    const prevElem = prevEntry[0] as ListItemElement // ä¸ä¸ä¸ªèç¹
    const { level: prevLevel = 0, type: prevType, ordered: prevOrdered } = prevElem

    // type ä¸ä¸è´ï¼éåºå¾ªç¯ï¼ä¸åç´¯å  num
    if (prevType !== type) { break }
    // prevLevel æ´å°ï¼éåºå¾ªç¯ï¼ä¸åç´¯å  num
    if (prevLevel < level) { break }

    if (prevLevel === level) {
      // level ä¸æ ·ï¼å¦æ ordered ä¸ä¸æ ·ï¼åéåºå¾ªç¯ï¼ä¸åç´¯å  num
      if (prevOrdered !== ordered) { break } else {
        // level ä¸æ ·ï¼order ä¸æ ·ï¼åç´¯å  num
        num += 1
      }
    }

    // prevLevel æ´å¤§ï¼ä¸ç´¯å  num ï¼ç»§ç»­åå
    curElem = prevElem
    curPath = prevPath
  }

  return num
}

function renderListElem(
  elemNode: SlateElement,
  children: VNode[] | null,
  editor: IDomEditor,
): VNode {
  ELEM_TO_EDITOR.set(elemNode, editor) // è®°å½ elem å editor å³ç³»ï¼elem-to-html æ¶è¦ç¨

  const { level = 0, ordered = false } = elemNode as ListItemElement

  // æ ¹æ® level å¢å  margin-left
  const listStyle = {
    margin: `5px 0 5px ${level * 20}px`,
    display: 'flex',
    alignItems: 'flex-start',
  }

  // list-item åç¼
  let prefix = ''

  if (ordered) {
    // æåºåè¡¨ï¼è·ååç¼ number
    const orderedNumber = getOrderedItemNumber(editor, elemNode)

    prefix = `${orderedNumber}.`
  } else {
    // æ åºåè¡¨ï¼æ ¹æ®å±çº§ï¼ä½¿ç¨ä¸åçåç¼ç¬¦å·
    prefix = genPreSymbol(level)
  }

  // è·ååç¼é¢è²
  const prefixColor = getListItemColor(elemNode)

  const vnode = (
    <div style={listStyle}>
      <span
        contentEditable={false}
        style={{ marginRight: '0.5em', color: prefixColor }}
        data-w-e-reserve
      >
        {prefix}
      </span>
      <span style={{
        flex: '1',
        wordBreak: 'break-word',
      }}>{children}</span>
    </div>
  )

  return vnode
}

const renderListItemConf = {
  type: 'list-item',
  renderElem: renderListElem,
}

export default renderListItemConf
