import { DomEditor, IDomEditor } from '@wangeditor-next/core'
import {
  Editor, Range, Transforms,
} from 'slate'

import { ListItemElement } from './custom-types'
import { getBrotherListNodeByLevel } from './helpers'

/**
 * è·åéä¸­ç top elems
 * @param editor editor
 */
function getTopSelectedElemsBySelection(editor: IDomEditor) {
  return Editor.nodes(editor, {
    at: editor.selection || undefined,
    match: n => DomEditor.findPath(editor, n).length === 1, // åªå¹éé¡¶çº§åç´ 
  })
}

function withList<T extends IDomEditor>(editor: T): T {
  const {
    deleteBackward, handleTab, normalizeNode, insertBreak,
  } = editor
  const newEditor = editor

  // éå insertBreak - ç©º list ç¹å»åè½¦æ¶å é¤è¯¥ç©º list
  newEditor.insertBreak = () => {
    const [nodeEntry] = Editor.nodes(editor, {
      match: n => DomEditor.checkNodeType(n, 'list-item'),
      universal: true,
    })

    if (!nodeEntry) { return insertBreak() }
    const listElem = nodeEntry[0] as ListItemElement

    if (listElem.children[0].text === '') {
      Transforms.setNodes(newEditor, {
        type: 'paragraph',
        // @ts-ignore
        ordered: undefined,
        level: undefined,
      })
      return
    }
    return insertBreak()
  }

  // éå deleteBackward - éä½ level æèè½¬æ¢ä¸º p åç´ 
  newEditor.deleteBackward = unit => {
    const { selection } = newEditor

    if (selection == null) {
      deleteBackward(unit)
      return
    }

    if (Range.isExpanded(selection)) {
      deleteBackward(unit)
      return
    }

    const listItemElem = DomEditor.getSelectedNodeByType(newEditor, 'list-item')

    if (listItemElem == null) {
      // æªå¹éå° list-item
      deleteBackward(unit)
      return
    }

    if (selection.focus.offset === 0) {
      // éä¸­äºå½å list-item ææ¬çå¼å¤´ï¼æ­¤æ¶æå é¤é®ï¼åºè¯¥éä½ level æè½¬æ¢ä¸º p åç´ 
      const { level = 0, ordered = false } = listItemElem as ListItemElement

      if (level > 0) {
        // å¦ææåå¼èç¹ï¼åå¤æ­ ordered æ¯å¦ä¸è´ï¼ä¸ä¸è´éè¦åæ¢ ordered
        const brotherElem = getBrotherListNodeByLevel(
          editor,
          listItemElem as ListItemElement,
          level - 1,
        )

        if (brotherElem && brotherElem.ordered !== ordered) {
          Transforms.setNodes(newEditor, { level: level - 1, ordered: !ordered })
        } else { Transforms.setNodes(newEditor, { level: level - 1 }) }
      } else {
        // è½¬æ¢ä¸º p åç´ 
        Transforms.setNodes(newEditor, {
          type: 'paragraph',
          // @ts-ignore
          ordered: undefined,
          level: undefined,
        })
      }
      return
    }

    // å¶ä»æåµ
    deleteBackward(unit)
  }

  // éå tab - å½éä¸­ list-item ææ¬å¼å¤´æ¶ï¼å¢å  level
  newEditor.handleTab = () => {
    const { selection } = newEditor

    if (selection == null) {
      handleTab()
      return
    }

    // éåºæ¯åå¹¶çï¼å¤æ­åä¸ª list-item å³å¯
    if (Range.isCollapsed(selection)) {
      const listItemElem = DomEditor.getSelectedNodeByType(newEditor, 'list-item')

      if (listItemElem == null) {
        // æªå¹éå° list-item
        handleTab()
        return
      }

      if (selection.focus.offset === 0) {
        // éä¸­äºå½å list-item ææ¬çå¼å¤´ï¼æ­¤æ¶æ tab åºè¯¥å¢å  level
        const { level = 0 } = listItemElem as ListItemElement

        Transforms.setNodes(newEditor, { level: level + 1 })
        return
      }
    }

    // éåºæ¯å±å¼çï¼è¦å¤æ­å¤ä¸ª list-item
    if (Range.isExpanded(selection)) {
      let listItemNum = 0 // éä¸­ç list-item æå ä¸ª
      let hasOtherElem = false // æ¯å¦æå¶ä»åç´ 

      for (const entry of getTopSelectedElemsBySelection(newEditor)) {
        const [elem] = entry
        const type = DomEditor.getNodeType(elem)

        if (type === 'list-item') { listItemNum += 1 } else { hasOtherElem = true }
      }

      if (hasOtherElem || listItemNum <= 1) {
        // éä¸­äºå¶ä»åç´ ï¼æèåªéä¸­ä¸ä¸ª list-item ï¼åæ§è¡é»è®¤è¡ä¸º
        handleTab()
        return
      }

      // æªéä¸­å¶ä»åç´ ï¼ä¸éä¸­å¤ä¸ª list-item ï¼åå¢å  level
      for (const entry of getTopSelectedElemsBySelection(newEditor)) {
        const [elem, path] = entry
        const { level = 0 } = elem as ListItemElement

        Transforms.setNodes(newEditor, { level: level + 1 }, { at: path })
      }
      return
    }

    // å¶ä»æåµ
    handleTab()
  }

  // å¼å®¹ä¹åç JSON æ ¼å¼ `numbered-list` å `bulleted-list` ï¼ä¹åç list æ²¡æåµå¥åè½ï¼
  newEditor.normalizeNode = ([node, path]) => {
    const type = DomEditor.getNodeType(node)

    if (type === 'bulleted-list' || type === 'numbered-list') {
      Transforms.unwrapNodes(newEditor, { at: path })
    }

    // æ§è¡é»è®¤è¡ä¸º
    return normalizeNode([node, path])
  }

  return newEditor
}

export default withList
